const getGlobal = () => (typeof window !== 'undefined' ? window : global)


const getTinymce = (jsPath) => {
  const global = getGlobal()
  const tinymce = global && global.tinymce ? global.tinymce : null
  if (tinymce) {
    tinymce.suffix = '.min'
    tinymce.baseURL = jsPath
  }
  return tinymce
}
